/* Pricing */
  .border-radius{
    border-radius: .5rem .5rem 0 0;
  }
  .border-right-blue{
    border-right: .1em solid $pricing-blue;
  }
  .border-left-blue{
    border-left: .1em solid $pricing-blue;
  }
